import React from 'react'
import Article from './Article'
import './articles.scss'

const Articles = ({ data }) => {
    return (
        <div className="articles">
            {data.map( article => {
                    const data = article.node
                    return <Article key={data.id} data={data} /> 
                }
            )}
        </div>
    )
}

export default Articles